html {
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #38bbb4 #dddddd;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #DDD;
}

html::-webkit-scrollbar-thumb {
  background-color: #38BBB4;
}

body {
  font-family: 'frt', sans-serif;
  direction: rtl;
  text-align: right;
  overflow-y: hidden;
}

* {
  font-family: inherit;
}

input, a, button {
  font-family: 'frt', sans-serif;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
  padding: 0;
  margin: 0;
  list-style: none;
}

@keyframes hov-animate {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.fst-back-img {
  position: absolute;
  top: 0px;
  right: -285px;
}

.sec-one {
  min-height: 850px;
  overflow: hidden;
}

.sec-one .container {
  position: relative;
}

.nav-bar .pos {
  align-items: center;
  flex-direction: row-reverse;
  padding: 33px 0 0 0;
}

.nav-bar .pos .nav-list {
  display: flex;
  font-family: frtBold;
  align-items: center;
  flex-direction: row-reverse;
  padding: 23px 0 0 0;
}

.nav-bar .pos .nav-list ul {
  align-items: center;
  flex-direction: row-reverse;
}

.nav-bar .pos .nav-list ul .md-v {
