}

.nav-bar .pos .nav-list ul li:hover a {
  color: #38bbb4;
}

.nav-bar .pos .nav-list ul li button {
  font-family: frtBold;
  font-size: 14px;
  color: #FFF;
  background-image: linear-gradient(to right, #40e497, #3bb5b6, #3bb5b6, #40e497);
  background-size: 200%;
  width: 170px;
  height: 40px;
  border: none;
  border-radius: 50px;
  padding: 0 0 0 0;
  transition: all .3s ease-in-out;
  background-position: left;
}

.nav-bar .pos .nav-list ul li button .ur-i {
